.helper {
    height: 500px;
    background-color: orange;
}
.namediv {
    height: 100vh;
    width: 100%;
    margin: auto;
    overflow-x: hidden;
    /* position: relative; */
    background-color: #030200;
    z-index: 2;
    display: flex;
    align-items: center;
    overflow-y: hidden;
}

.name {
   position: absolute;
   top: 30%;
   right: 60%;
   /* color: white; */
   font-size: 3.5em;
   background-image: linear-gradient(90deg,#c70000, #c70000, #c70000 );
   background-clip: text;
   -webkit-text-stroke-color: transparent;
   -webkit-text-stroke-width: calc(1.5em /16);
   z-index: 3;
}